<template>
	<div class="after-sales">
		<div class="order-details bottom-space">
			<div class="order-icon"></div>
			<div>
				<p class="order-title"></p>
				<p class="order-style">::L:</p>
			</div>
		</div>
		<div class="cause bottom-space">
			<div class="select-box">
				<div></div>
				<select name="" id="" dir="rtl">
					<option selected="selected" class="hide"></option>
					<option value="1"></option>
					<option value="2"></option>
					<option value="3"></option>
				</select>
			</div>
			<div class="text">
				<p></p>
				<p></p>
			</div>
		</div>
		<div class="describe">
			<div class="title"></div>
			<textarea name="" id="" cols="30" rows="10"></textarea>
		</div>
		<div class="file">
			<input class="hide" type="file" accept="image/*">
			<div class="camera"><img src="../assets/ico_camera.png"></div>
		</div>
		<div class="submit"></div>
	</div>
</template>

<script>
export default {
	name: 'AfterSales',
	data () {
		return {

		}
	},
	created (){
		document.title="";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		}
	}
}
</script>

<style scoped>
	.order-details{
		display: flex;
		display: -webkit-flex;
	}
	.order-icon{
		width: 5rem;
		height: 5rem;
		background: url("../assets/clothes-bg.png") center left no-repeat;background-size: 80% auto;
		flex-shrink:0;
		-webkit-flex-shrink:0;
	}
	.select-box{
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
	}
	.order-style{
		color: #999;
	}
	.cause .text{
		color: #999;
	}
	.describe .title{
		color: #666;
	}
</style>



